import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';


const url = "https://yellow-website.com/yw-d9/jsonapi/node/client_details";

const initialState = {
  loading_client: false,
  client: [],
  error_client: '',
}


//Generate pending, fulfilled, and rejected action types
export const clientData = createAsyncThunk("client_detail/clientDetail", () => {
  
return axios({
    method: 'GET',
    url: url
  })
  .then(response => response.data)
  .catch(error => console.log(error))
    
  
})


const dataSlice = createSlice({
  name: 'client_detail', 
  initialState,
  
  extraReducers: (builder) => {
    console.log("builder",builder)
    builder.addCase(clientData.pending, (state)=> {
        state.loading_client = true
    })
    builder.addCase(clientData.fulfilled, (state, action)=>{
      //console.log(state, action)
      state.loading_client = false
      state.client = action.payload
      state.error_client = ''
    })
    builder.addCase(clientData.rejected,(state, action)=>{
      state.loading_client = false
      state.client = []
      state.error_client = action.error.message
    })
  },
})

export default dataSlice.reducer